import { useEffect, useState } from "react";
import { useQueryClient } from "@tanstack/react-query";
import {
  axiosDelete,
  axiosGet,
  axiosPost,
  axiosPut,
} from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import { NothingFound } from "@/assets/IntegrationIcons";
import ThreeDot from "@/components/loaders/ThreeDot";

function Twilio({ onClose }) {
  const queryClient = useQueryClient();
  const [state, setState] = useState<any>(0);
  const [phoneNumber, setPhone] = useState<string | undefined>();
  const [accountId, setAccountSID] = useState<string | undefined>();
  const [authToken, setAuthToken] = useState<string | undefined>();
  const [configName, setConfigName] = useState<string | undefined>();
  const [accountType, setAccountType] = useState("twilio");
  const [loader, setLoader] = useState(true);
  const [saveLoader, setSaveLoader] = useState<any>(false);
  const [allSmsData, setAllSmsData] = useState<any>();
  const [reloadState, setReloadState] = useState(0);
  const [editId, setEditId] = useState();
  const [testPhoneNumber, setTestPhoneNumber] = useState<string | undefined>();
  const [messagingServiceSid, setMessagingServiceSid] = useState<
    string | undefined
  >();
  const [editSmsData, setEditSmsData] = useState<any>();
  const [confimationState, setConfirmationState] = useState<any>();
  const [disableSubmit, setDisableSubmit] = useState<boolean>(false);
  const dispatch = useDispatch();

  useEffect(() => {
    getAllSmsConfig();
    if (allSmsData && allSmsData.length === 0) {
      setState(1);
    }
  }, [reloadState, state]);

  const handleEditData = (data) => {
    setConfigName(data.configName);
    setTestPhoneNumber(data.testPhoneNumber);
    setPhone(data.phoneNumber);
    setMessagingServiceSid(data.messagingServiceSid);
    setAuthToken(data.authToken);
    setAccountSID(data.accountId);
  };

  const getAllSmsConfig = () => {
    setLoader(true);
    axiosGet("/configurations/getAllSmsConfig")
      .then((response) => {
        setLoader(false);
        setAllSmsData(response.data.data);
        setConfirmationState(Array(response.data?.data.length).fill(false));
        setReloadState(1);
      })
      .catch((err) => {
        setLoader(false);
        dispatch(
          userSlice.actions.setInfoModal({
            showModal: true,
            modalType: "error",
            modalTitle: "Oops!",
            modalMessage: err.response.data.message,
          })
        );
      });
  };

  const handelSubmit = () => {
    if (disableSubmit) return;
    setDisableSubmit(true);
    setSaveLoader(true);
    setReloadState(1);
    if (
      phoneNumber !== "" &&
      accountId !== "" &&
      authToken !== "" &&
      configName !== ""
    ) {
      if (messagingServiceSid !== "") {
        axiosPost("/configurations/addSmsConfig", {
          phoneNumber,
          testPhoneNumber,
          accountId,
          authToken,
          configName,
          accountType,
          messagingServiceSid,
        })
          .then(() => {
            setDisableSubmit(false);
            setSaveLoader(false);
            setState(0);
            //onClose();
            dispatch(
              userSlice.actions.setInfoModal({
                showModal: true,
                modalType: "success",
                modalTitle: "Success",
                modalMessage: "Configuration added successfully",
              })
            );
            dispatch(userSlice.actions.setIntegration(true));
            setReloadState(1);
            queryClient.invalidateQueries({
              queryKey: ["configurations", "sms"],
            });
          })
          .catch((err) => {
            setDisableSubmit(false);
            setSaveLoader(false);
            dispatch(
              userSlice.actions.setToastPopUp({
                show: true,
                toastType: "error",
                message: `${
                  err.response?.data.message
                    ? err.response.data.message
                    : err.message
                }`,
              })
            );
          });
      } else {
        axiosPost("/configurations/addSmsConfig", {
          phoneNumber,
          testPhoneNumber,
          accountId,
          authToken,
          configName,
          accountType,
        })
          .then(() => {
            setDisableSubmit(false);
            setLoader(false);
            setState(0);
            //onClose();
            dispatch(
              userSlice.actions.setInfoModal({
                showModal: true,
                modalType: "success",
                modalTitle: "Success",
                modalMessage: "Configuration added successfully",
              })
            );
            setReloadState(1);
          })
          .catch((err) => {
            setDisableSubmit(false);
            setLoader(false);
            dispatch(
              userSlice.actions.setInfoModal({
                showModal: true,
                modalType: "error",
                modalTitle: "Oops!",
                modalMessage: err.response.data.message,
              })
            );
          });
      }
    } else {
      setDisableSubmit(false);
      dispatch(
        userSlice.actions.setInfoModal({
          showModal: true,
          modalType: "warning",
          modalTitle: "Warning",
          modalMessage: "Please fill all the fields",
        })
      );
    }
  };

  const handleDelete = (id) => {
    axiosDelete(`/configurations/deleteSmsConfig?configId=${id}`)
      .then((response) => {
        setReloadState(2);
        //onClose();
        dispatch(
          userSlice.actions.setInfoModal({
            showModal: true,
            modalType: "success",
            modalTitle: "Success",
            modalMessage: "Configration Delete Successfully",
          })
        );
        dispatch(userSlice.actions.setIntegration(false));
        queryClient.invalidateQueries({
          queryKey: ["configurations", "sms"],
        });
      })
      .catch((err) => {
        dispatch(
          userSlice.actions.setInfoModal({
            showModal: true,
            modalType: "error",
            modalTitle: "Oops!",
            modalMessage: err.response.data.message,
          })
        );
      });
  };

  const handleEdit = () => {
    if (disableSubmit) return;
    setDisableSubmit(true);
    setLoader(true);
    const editConfigName =
      configName?.length === 0 ? editSmsData.configName : configName;
    const editPhoneNumber =
      phoneNumber?.length === 0 ? editSmsData.phoneNumber : phoneNumber;
    const editTestPhoneNumber =
      testPhoneNumber?.length === 0
        ? editSmsData.testPhoneNumber
        : testPhoneNumber;
    const editAccountId =
      accountId?.length === 0 ? editSmsData.accountId : accountId;
    const editAccountType =
      accountType?.length === 0 ? editSmsData.accountType : accountType;
    const editAuthToken =
      authToken?.length === 0 ? editSmsData.authToken : authToken;
    axiosPut(`/configurations/updateSmsconfig?configId=${editId}`, {
      phoneNumber: editPhoneNumber,
      testPhoneNumber: editTestPhoneNumber,
      accountId: editAccountId,
      authToken: editAuthToken,
      accountType: editAccountType,
      configName: editConfigName,
    })
      .then(() => {
        setDisableSubmit(false);
        setLoader(false);
        setReloadState(5);
        setState(0);
        //onClose();
        dispatch(
          userSlice.actions.setInfoModal({
            showModal: true,
            modalType: "success",
            modalTitle: "Success",
            modalMessage: "Data saved",
          })
        );
        queryClient.invalidateQueries({
          queryKey: ["configurations", "sms"],
        });
      })
      .catch((err) => {
        setDisableSubmit(false);
        setLoader(false);
        dispatch(
          userSlice.actions.setInfoModal({
            showModal: true,
            modalType: "error",
            modalTitle: "Oops!",
            modalMessage: err.response.data.message,
          })
        );
      });
  };

  return (
    <>
      <div className="overflow new-scrollbar" onMouseDown={() => onClose()}>
        <div className="integration-modal">
          <div
            className="integration-content"
            onMouseDown={(e) => e.stopPropagation()}
          >
            <div className="modal-head">
              <h2>Send Omniengage Data To Twilio</h2>
              <svg
                width="39"
                height="39"
                viewBox="0 0 39 39"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M19.4714 0.0284424C8.73645 0.0284424 0 8.76489 0 19.4999C0 30.2349 8.73645 38.9713 19.4714 38.9713C30.2635 39.0284 39 30.292 39 19.4999C39 8.70779 30.2635 0.0284424 19.4714 0.0284424ZM19.4714 33.8893C11.5915 33.8893 5.13909 27.4369 5.13909 19.4999C5.13909 11.6199 11.5915 5.16753 19.4714 5.16753C27.4085 5.16753 33.8609 11.6199 33.8609 19.4999C33.8609 27.4369 27.4085 33.8893 19.4714 33.8893Z"
                  fill="#E31E26"
                />
                <path
                  d="M24.3247 18.7005C26.5637 18.7005 28.3789 16.8854 28.3789 14.6463C28.3789 12.4073 26.5637 10.5922 24.3247 10.5922C22.0856 10.5922 20.2705 12.4073 20.2705 14.6463C20.2705 16.8854 22.0856 18.7005 24.3247 18.7005Z"
                  fill="#E31E26"
                />
                <path
                  d="M24.3247 28.4077C26.5637 28.4077 28.3789 26.5925 28.3789 24.3535C28.3789 22.1144 26.5637 20.2993 24.3247 20.2993C22.0856 20.2993 20.2705 22.1144 20.2705 24.3535C20.2705 26.5925 22.0856 28.4077 24.3247 28.4077Z"
                  fill="#E31E26"
                />
                <path
                  d="M14.6176 28.4077C16.8567 28.4077 18.6718 26.5925 18.6718 24.3535C18.6718 22.1144 16.8567 20.2993 14.6176 20.2993C12.3786 20.2993 10.5635 22.1144 10.5635 24.3535C10.5635 26.5925 12.3786 28.4077 14.6176 28.4077Z"
                  fill="#E31E26"
                />
                <path
                  d="M14.6176 18.7005C16.8567 18.7005 18.6718 16.8854 18.6718 14.6463C18.6718 12.4073 16.8567 10.5922 14.6176 10.5922C12.3786 10.5922 10.5635 12.4073 10.5635 14.6463C10.5635 16.8854 12.3786 18.7005 14.6176 18.7005Z"
                  fill="#E31E26"
                />
              </svg>

              <div
                className="closing-modal"
                onClick={() => {
                  onClose();
                }}
              >
                <button>
                  <svg
                    width="10"
                    height="10"
                    viewBox="0 0 10 10"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                      fill="#6D6D6D"
                    />
                  </svg>
                </button>
              </div>
            </div>
            <div className="modal-content-box">
              {state == 0 && (
                <div className="for-connect">
                  <div className="modal-content-head">
                    <h1>Connect</h1>
                    <p>
                      Select Twillio account and then proceed to add new
                      configuration
                    </p>
                  </div>

                  <div className="modal-content-table new-scrollbar">
                    <ul className="modal-content-list">
                      {loader ? (
                        <>
                          <li className={"react-loading-skeleton"}> </li>
                          <li className={"react-loading-skeleton"}> </li>
                          <li className={"react-loading-skeleton"}> </li>
                          <li className={"react-loading-skeleton"}> </li>
                          <li className={"react-loading-skeleton"}> </li>
                        </>
                      ) : allSmsData && allSmsData?.length !== 0 ? (
                        allSmsData.map((data, index) => (
                          <li
                            key={index}
                            style={{
                              display: confimationState?.[index]
                                ? "block"
                                : "flex",
                            }}
                          >
                            <div className="list-left">
                              <label className="label-modal">
                                {data.configName}
                              </label>
                            </div>
                            {confimationState?.[index] ? (
                              <div className="already-connected">
                                <div className="connected-left">
                                  <svg
                                    width="20"
                                    height="19"
                                    viewBox="0 0 20 19"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                  >
                                    <path
                                      fillRule="evenodd"
                                      clipRule="evenodd"
                                      d="M12.1174 1.66476C11.9257 1.26545 11.625 0.928445 11.2501 0.692568C10.8752 0.456691 10.4412 0.331543 9.99826 0.331543C9.5553 0.331543 9.12136 0.456691 8.74643 0.692568C8.3715 0.928445 8.07084 1.26545 7.87909 1.66476L1.11576 14.9689C0.306594 16.5573 1.32909 18.6664 3.23409 18.6664H16.7616C18.6674 18.6664 19.6883 16.5581 18.8808 14.9689L12.1174 1.66476ZM9.99826 6.16642C10.2193 6.16642 10.4312 6.25422 10.5875 6.4105C10.7438 6.56678 10.8316 6.77874 10.8316 6.99976V11.1664C10.8316 11.3874 10.7438 11.5994 10.5875 11.7557C10.4312 11.912 10.2193 11.9998 9.99826 11.9998C9.77725 11.9998 9.56528 11.912 9.409 11.7557C9.25272 11.5994 9.16493 11.3874 9.16493 11.1664V6.99976C9.16493 6.77874 9.25272 6.56678 9.409 6.4105C9.56528 6.25422 9.77725 6.16642 9.99826 6.16642ZM9.99826 13.2498C10.2193 13.2498 10.4312 13.3376 10.5875 13.4938C10.7438 13.6501 10.8316 13.8621 10.8316 14.0831V14.4998C10.8316 14.7208 10.7438 14.9327 10.5875 15.089C10.4312 15.2453 10.2193 15.3331 9.99826 15.3331C9.77725 15.3331 9.56528 15.2453 9.409 15.089C9.25272 14.9327 9.16493 14.7208 9.16493 14.4998V14.0831C9.16493 13.8621 9.25272 13.6501 9.409 13.4938C9.56528 13.3376 9.77725 13.2498 9.99826 13.2498Z"
                                      fill="#FB5F66"
                                    />
                                  </svg>
                                  <div className="connect-left-content">
                                    <p>
                                      Are you sure you want to delete the
                                      configuration?{" "}
                                    </p>
                                    <span>
                                      Please note that this action will
                                      permanently remove the configuration from
                                      the entire application and cannot be
                                      undone.
                                    </span>
                                  </div>
                                </div>
                                <div className="connected-right">
                                  <button
                                    className="replace-btn"
                                    onClick={() => {
                                      handleDelete(data._id);
                                    }}
                                  >
                                    Delete
                                  </button>
                                  <hr />
                                  <button
                                    className="cncl-btn"
                                    onClick={() => {
                                      setConfirmationState(
                                        confimationState.map(() => false),
                                      );
                                    }}
                                  >
                                    Cancel
                                  </button>
                                </div>
                              </div>
                            ) : (
                              <div className="list-right">
                                <div
                                  className="edit-int-modal"
                                  onClick={() => {
                                    setEditSmsData(data);
                                    setEditId(data._id);
                                    setState(2);
                                    handleEditData(data);
                                  }}
                                >
                                  <svg
                                    width={16}
                                    height={16}
                                    viewBox="0 0 16 16"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                  >
                                    <path
                                      fillRule="evenodd"
                                      clipRule="evenodd"
                                      d="M14.5727 1.42908C13.5224 0.378755 12.465 -0.0896205 11.3984 0.0150696C10.3696 0.11606 9.51687 0.736551 8.82434 1.42908L1.27559 8.97774C1.06131 9.19202 0.876557 9.47566 0.739338 9.74867C0.60241 10.0211 0.483125 10.342 0.439976 10.6497L0.439795 10.651L0.0285955 13.5294L0.0283784 13.5308C-0.0761641 14.2458 0.119452 14.9262 0.597669 15.4025C1.07487 15.8777 1.75504 16.0718 2.46593 15.974L2.46593 15.974L2.46959 15.9735L5.35305 15.5615C5.65679 15.5182 5.97538 15.3983 6.24587 15.2623C6.51794 15.1256 6.79986 14.9431 7.01559 14.7343L7.01566 14.7344L7.02399 14.7261L13.4216 8.32856C13.4713 8.29049 13.5162 8.24581 13.5548 8.19537L14.5727 7.17743C15.2653 6.4849 15.8858 5.63221 15.9868 4.60335C16.0915 3.53681 15.6231 2.47941 14.5727 1.42908ZM12.7791 6.93896L13.5567 6.16136C14.167 5.55108 14.5044 4.99559 14.5567 4.46298C14.6053 3.96806 14.4223 3.31073 13.5567 2.44516C12.6911 1.57958 12.0337 1.39655 11.5388 1.44513C11.0062 1.49741 10.4507 1.83487 9.8404 2.44516L9.06606 3.21948C9.67791 4.94966 11.041 6.31628 12.7791 6.93896ZM7.96361 4.32192L2.29165 9.99381C2.21855 10.0669 2.11591 10.2096 2.02322 10.394C1.93043 10.5786 1.87743 10.7458 1.86294 10.8496L1.86262 10.8519L1.45069 13.7353L1.4503 13.738C1.40205 14.0667 1.49867 14.2718 1.61167 14.3843C1.72582 14.498 1.93606 14.596 2.26841 14.5507L2.27003 14.5505L5.14984 14.1391C5.24845 14.125 5.41363 14.0724 5.60057 13.9784C5.78329 13.8866 5.93084 13.7834 6.01282 13.7051L11.6781 8.03995C10.0341 7.28288 8.71497 5.96326 7.96361 4.32192Z"
                                      fill="#6D6D6D"
                                    />
                                  </svg>
                                </div>
                                <div
                                  className="del-int-modal"
                                  onClick={() =>
                                    setConfirmationState(
                                      confimationState.map((val, ind) =>
                                        index === ind ? true : false,
                                      ),
                                    )
                                  }
                                >
                                  <svg
                                    width={14}
                                    height={16}
                                    viewBox="0 0 14 16"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                  >
                                    <path
                                      d="M13.3322 2.66667H11.2656C11.1108 1.91428 10.7014 1.23823 10.1064 0.75248C9.51134 0.266727 8.76702 0.000969685 7.99888 0L6.66554 0C5.8974 0.000969685 5.15308 0.266727 4.55804 0.75248C3.96299 1.23823 3.5536 1.91428 3.39887 2.66667H1.3322C1.15538 2.66667 0.985814 2.73691 0.86079 2.86194C0.735765 2.98696 0.665527 3.15653 0.665527 3.33334C0.665527 3.51015 0.735765 3.67972 0.86079 3.80475C0.985814 3.92977 1.15538 4.00001 1.3322 4.00001H1.99886V12.6667C1.99992 13.5504 2.35145 14.3977 2.97635 15.0226C3.60124 15.6474 4.44847 15.999 5.33221 16H9.33222C10.2159 15.999 11.0632 15.6474 11.6881 15.0226C12.313 14.3977 12.6645 13.5504 12.6656 12.6667V4.00001H13.3322C13.509 4.00001 13.6786 3.92977 13.8036 3.80475C13.9287 3.67972 13.9989 3.51015 13.9989 3.33334C13.9989 3.15653 13.9287 2.98696 13.8036 2.86194C13.6786 2.73691 13.509 2.66667 13.3322 2.66667ZM6.66554 1.33334H7.99888C8.4124 1.33384 8.81563 1.46225 9.15329 1.70097C9.49095 1.93968 9.74649 2.277 9.88488 2.66667H4.77954C4.91793 2.277 5.17347 1.93968 5.51113 1.70097C5.84879 1.46225 6.25202 1.33384 6.66554 1.33334ZM11.3322 12.6667C11.3322 13.1971 11.1215 13.7058 10.7464 14.0809C10.3714 14.456 9.86265 14.6667 9.33222 14.6667H5.33221C4.80177 14.6667 4.29306 14.456 3.91799 14.0809C3.54291 13.7058 3.3322 13.1971 3.3322 12.6667V4.00001H11.3322V12.6667Z"
                                      fill="#fb5f66"
                                    />
                                    <path
                                      d="M5.9987 12C6.17551 12 6.34508 11.9298 6.4701 11.8048C6.59513 11.6797 6.66537 11.5102 6.66537 11.3334V7.33336C6.66537 7.15654 6.59513 6.98697 6.4701 6.86195C6.34508 6.73693 6.17551 6.66669 5.9987 6.66669C5.82189 6.66669 5.65232 6.73693 5.52729 6.86195C5.40227 6.98697 5.33203 7.15654 5.33203 7.33336V11.3334C5.33203 11.5102 5.40227 11.6797 5.52729 11.8048C5.65232 11.9298 5.82189 12 5.9987 12ZM8.66537 12C8.84218 12 9.01175 11.9298 9.13678 11.8048C9.2618 11.6797 9.33204 11.5102 9.33204 11.3334V7.33336C9.33204 7.15654 9.2618 6.98697 9.13678 6.86195C9.01175 6.73693 8.84218 6.66669 8.66537 6.66669C8.48856 6.66669 8.31899 6.73693 8.19397 6.86195C8.06894 6.98697 7.9987 7.15654 7.9987 7.33336V11.3334C7.9987 11.5102 8.06894 11.6797 8.19397 11.8048C8.31899 11.9298 8.48856 12 8.66537 12Z"
                                      fill="#fb5f66"
                                    />
                                  </svg>
                                </div>
                              </div>
                            )}
                          </li>
                        ))
                      ) : (
                        <div className="no-invoice-found">
                          <NothingFound />
                          <h1>Nothing!!</h1>
                          <p>Your Twillio configurations are empty </p>
                        </div>
                      )}
                      {/* {!loader && allAIConfig && allAIConfig.length === 0 && <p> Please Add OPENAI configurations </p>} */}
                    </ul>
                  </div>
                </div>
              )}
              {state == 1 && (
                <div className="for-map">
                  <div className="modal-content-head">
                    <h1>Map</h1>
                    <p>
                      Select a Twillio list you want your Omniengage leads added
                      to
                    </p>
                  </div>
                  <div className="modal-content-map-fields">
                    <div className="map-input">
                      <label>
                        Config name <span className="star">*</span>{" "}
                      </label>
                      <input
                        type="text"
                        placeholder="Config Name"
                        onChange={(e) => {
                          setConfigName(e.target.value);
                        }}
                      />
                    </div>
                    <div className="flex-map-inputs">
                      <div className="map-input">
                        <label>
                          TWILIO PHONE NUMBER <span className="star">*</span>{" "}
                        </label>
                        <input
                          type="text"
                          placeholder="Ex: 1772XXXXXXX"
                          value={phoneNumber}
                          onChange={(e) => {
                            const phoneNum: string = e.target.value.replaceAll(
                              /[^0-9]/g,
                              "",
                            );
                            setPhone(phoneNum.slice(0, 20));
                          }}
                        />
                      </div>
                      <div className="map-input dropdown-ssh">
                        <label>Messaging Service SID </label>
                        <input
                          type="text"
                          placeholder="Messaging Service Id"
                          onChange={(e) => {
                            setMessagingServiceSid(e.target.value);
                          }}
                        ></input>
                      </div>
                    </div>
                    <div className="flex-map-inputs">
                      <div className="map-input">
                        <label>
                          Twilio Verified Caller ID{" "}
                          <span className="star">*</span>{" "}
                        </label>
                        <input
                          type="text"
                          placeholder="Ex: 1778XXXXXXX"
                          value={testPhoneNumber}
                          onChange={(e) => {
                            const phoneNum: string = e.target.value.replaceAll(
                              /[^0-9]/g,
                              "",
                            );
                            setTestPhoneNumber(phoneNum.slice(0, 20));
                          }}
                        />
                      </div>
                      <div className="map-input">
                        <label>
                          Account SID <span className="star">*</span>{" "}
                        </label>
                        <input
                          type="text"
                          placeholder="Account SID"
                          onChange={(e) => {
                            setAccountSID(e.target.value);
                          }}
                        />
                      </div>
                    </div>
                    <div className="flex-map-inputs">
                      <div className="map-input no-m">
                        <label>
                          Auth Token <span className="star">*</span>{" "}
                        </label>
                        <input
                          type="text"
                          placeholder="Auth Token"
                          onChange={(e) => {
                            setAuthToken(e.target.value);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              )}
              {state == 2 && (
                <div className="for-map">
                  <div className="modal-content-head">
                    <h1>Edit</h1>
                    <p>
                      Select a Twillio list you want your Omniengage leads added
                      to
                    </p>
                  </div>
                  <div className="modal-content-map-fields">
                    <div className="map-input">
                      <label>
                        Config name <span className="star">*</span>
                      </label>
                      <input
                        type="text"
                        value={configName}
                        onChange={(e) => {
                          setConfigName(e.target.value);
                        }}
                      />
                    </div>
                    <div className="flex-map-inputs">
                      <div className="map-input">
                        <label>TWILIO PHONE NUMBER</label>
                        <input
                          type="text"
                          value={phoneNumber}
                          onChange={(e) => {
                            const phoneNum: string = e.target.value.replaceAll(
                              /[^0-9]/g,
                              "",
                            );
                            setPhone(phoneNum.slice(0, 20));
                          }}
                        />
                      </div>
                      <div className="map-input dropdown-ssh">
                        <label>Messaging Service SID (Optional)</label>
                        <input
                          type="text"
                          placeholder={messagingServiceSid}
                          onChange={(e) => {
                            setMessagingServiceSid(e.target.value);
                          }}
                        ></input>
                      </div>
                    </div>
                    <div className="flex-map-inputs">
                      <div className="map-input">
                        <label>Twilio Verified Caller ID</label>
                        <input
                          type="text"
                          value={testPhoneNumber}
                          onChange={(e) => {
                            const phoneNum: string = e.target.value.replaceAll(
                              /[^0-9]/g,
                              "",
                            );
                            setTestPhoneNumber(phoneNum.slice(0, 20));
                          }}
                        />
                      </div>
                      <div className="map-input">
                        <label>Account SID</label>
                        <input
                          type="text"
                          value={accountId}
                          onChange={(e) => {
                            setAccountSID(e.target.value);
                          }}
                        />
                      </div>
                    </div>
                    <div className="flex-map-inputs">
                      <div className="map-input no-m">
                        <label>Auth Token</label>
                        <input
                          type="text"
                          value={authToken}
                          onChange={(e) => {
                            setAuthToken(e.target.value);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </div>
            {/* {state === 0 && (
              <div className="modal-footer">
                <button onClick={() => setState(1)}>Proceed to Connect</button>
              </div>
            )} */}
            {!loader && state === 0 && (
              <div
                className="new-connections_outer"
                onClick={() => setState(1)}
              >
                <svg
                  width="18"
                  height="18"
                  viewBox="0 0 18 18"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M9 16.5C13.125 16.5 16.5 13.125 16.5 9C16.5 4.875 13.125 1.5 9 1.5C4.875 1.5 1.5 4.875 1.5 9C1.5 13.125 4.875 16.5 9 16.5Z"
                    stroke="#FB5F66"
                    strokeWidth="1.125"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                  <g>
                    <path
                      d="M6 9H12"
                      stroke="#FB5F66"
                      strokeWidth="1.125"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                    <path
                      d="M9 12V6"
                      stroke="#FB5F66"
                      strokeWidth="1.125"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </g>
                </svg>
                Add New Configuration
              </div>
            )}
            {state === 1 && (
              <div className="modal-footer">
                <button
                  onClick={() => {
                    handelSubmit();
                  }}
                  className="btn-valid"
                  disabled={
                    !phoneNumber ||
                    !testPhoneNumber ||
                    !accountId ||
                    !authToken ||
                    !configName
                  }
                >
                  {saveLoader ? "Adding..." : "Add Configuration"}
                </button>
              </div>
            )}
            {state === 2 && (
              <div className="modal-footer">
                <button className="modal_cls-btn" onClick={() => setState(0)}>
                  Back
                </button>
                <button
                  className="modal_cls_save-btn"
                  onClick={() => {
                    setEditId(editSmsData._id);
                    handleEdit();
                  }}
                >
                  {saveLoader ? (
                    <div className="fx al-fx-end jstfy-cnt gap-6">
                      Saving <ThreeDot />
                    </div>
                  ) : (
                    "Save Edited Fields"
                  )}
                </button>
              </div>
            )}
          </div>
        </div>
      </div>
    </>
  );
}

export default Twilio;
