import { useState, useEffect, useReducer } from "react";
import { axiosDelete, axiosGet, axiosPost } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import { NothingFound } from "@/assets/IntegrationIcons";
import Skeleton from "react-loading-skeleton";
import { useQueryClient } from "@tanstack/react-query";
import useEmailConfig from "@/pages/omni-builder/react-query/configs/useEmailConfig";

export default function Warmy({ onClose }) {
  const queryClient = useQueryClient();
  const [accounts, setAccounts] = useState<any>([]);
  const [componentLoader, setComponentLoader] = useState<boolean>(true);
  const [loader, setLoader] = useState<string[]>([]);

  const dispatch = useDispatch();

  const { data: emailConfig } = useEmailConfig();

  useEffect(() => {
    if (emailConfig) {
      setAccounts(emailConfig);
      setComponentLoader(false);
    }
  }, [emailConfig]);

  const connect = async (configId: string) => {
    setLoader([...loader, configId]);
    try {
      const res = await axiosPost(`/configurations/warmy?configId=${configId}`);
      if (res.data) {
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "success",
            message: `Email warming up successfully`,
          }),
        );

        setAccounts((prev) =>
          prev.map((account) =>
            account._id === configId
              ? { ...account, warmyStatus: "activate!" }
              : account,
          ),
        );
        queryClient.invalidateQueries({
          queryKey: ["configurations", "email"],
        });
      }
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            err.response?.data?.message ??
            err.message ??
            "Unable to warm up email account",
        }),
      );
    } finally {
      setLoader(loader.filter((id) => id !== configId));
    }
  };

  const disconnect = async (configId: string) => {
    setLoader([...loader, configId]);
    try {
      const res = await axiosDelete(
        `/configurations/warmy?configId=${configId}`,
      );
      if (res.status == 200) {
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "success",
            message: "Disconnected Successfully",
          }),
        );

        setAccounts((prev) =>
          prev.map((account) =>
            account._id === configId
              ? { ...account, warmyStatus: "pause!" }
              : account,
          ),
        );

        queryClient.invalidateQueries({
          queryKey: ["configurations", "email"],
        });
      }
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            err.response?.data?.message ??
            err.message ??
            "Something went wrong, please try again.",
        }),
      );
    } finally {
      setLoader(loader.filter((id) => id !== configId));
    }
  };

  return (
    <div className="general-container">
      <div
        className="overflow"
        onClick={() => {
          onClose();
        }}
      >
        <div
          className="no-data-add-button integration-content"
          onClick={(e) => {
            e.stopPropagation();
          }}
        >
          {/* MODAL HEADER */}
          <div className="modal-head">
            <h2>
              {componentLoader ? (
                <Skeleton height={15} width={250} />
              ) : (
                "Connect your e-mail account for warm up"
              )}
            </h2>
            {componentLoader ? (
              <Skeleton circle={true} height={30} width={30} />
            ) : (
              <img src="https://dlvkyia8i4zmz.cloudfront.net/ew7KFNKwS4SOf7bVNRxg_warmy__1_.svg" />
            )}

            <div className="closing-modal" onClick={onClose}>
              <button>
                <svg
                  width="10"
                  height="10"
                  viewBox="0 0 10 10"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                    fill="#6D6D6D"
                  />
                </svg>
              </button>
            </div>
          </div>
          <div className="fb-wrap">
            <div className="facebook-modal-content fx jstfy-sp-btw">
              <div className="skeletonPages">
                {/* <h1>
                  {loader ? (
                    <Skeleton height={10} width={305} />
                  ) : (
                    "Connect for Email Warm up"
                  )}{" "}
                </h1> */}
                <h2>
                  {componentLoader ? (
                    <Skeleton height={10} width={305} />
                  ) : (
                    "Warm up your email account"
                  )}{" "}
                </h2>
              </div>
            </div>
            <div className="modal-content-table new-scrollbar skeletonPages">
              {componentLoader ? (
                <>
                  <li className={"react-loading-skeleton"}> </li>
                  <li className={"react-loading-skeleton"}> </li>
                </>
              ) : accounts?.length > 0 ? (
                <ul className="modal-content-list">
                  {accounts?.map((data, index) => (
                    <li key={index} style={{ display: "flex" }}>
                      <div className="list-left">
                        <label className="label-modal">{data?.sender}</label>
                      </div>
                      <div className="list-right">
                        <div className="toggle-btn warmy-boolean">
                          <label className="switch">
                            {loader.includes(data._id) ? (
                              <div className="load-container">
                                <div className="linespinner"></div>
                              </div>
                            ) : (
                              <>
                                <input
                                  type="checkbox"
                                  name="custom"
                                  checked={data?.warmyStatus == "activate!"}
                                  onChange={() => {
                                    data?.warmyStatus == "activate!"
                                      ? disconnect(data._id)
                                      : connect(data._id);
                                  }}
                                />
                                <span className="toggle round"></span>
                              </>
                            )}
                          </label>
                        </div>
                      </div>
                    </li>
                  ))}
                </ul>
              ) : (
                <div className="no-invoice-found">
                  <NothingFound />
                  <h1>Nothing!!</h1>
                  <p>No Email Accounts Available</p>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
